
import React, { useState } from 'react';

export default function SelectList(props){
   
    return(
        <div>
            <h3 className={props.status === 1? 'statusText site-green' : 'no-show'}>
                Success
            </h3    >
            <h3 className={props.status === 0? 'statusText site-orange' : 'no-show'}>
                Failed (Site unsupported?)
            </h3>
        </div>
        
    )
}
