import React, { Component, useState,useEffect } from "react";
import { Link  } from "react-router-dom";
import axios from 'axios';
import Loading from './loading.component'


import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import TextField from 'material-ui/TextField'
import RaisedButton from 'material-ui/RaisedButton'
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import BEserv from '../config/BEserv'


export default function Navbar(props) {
    const [loading, setLoading] = useState(false)
    const [scrapeState,setScrapeState] = useState(-1)
    const [newUrl, setNewUrl] = useState("")
    const [sites,setSites] = useState([])
    const beLink = BEserv.beServ

    useEffect(() => {
        setLoading(true)
        axios.get(beLink+'/sites/getAll')
                            .then(res => {
                                    console.log(res)
                                    setSites(res.data)  
                                    setLoading(false)
                            })
                            .catch( err => console.log(err))
    },[]);

    function scrape(){
        setLoading(true)
       
        axios.get(beLink+'/items/scrapesite/')
                .then(res => {
                    setLoading(false)
                    setScrapeState(res.data)
                    // setTimeout(()=>{
                    //     setScrapeState(-1)
                    // },2000)
                });
    }

     
    function handleChange(event) {
        const {name, value} = event.target
        setNewUrl(value)
    }
    function _handleKeyDown(e){
        if (e.key === 'Enter') {
            setLoading(true)
            const siteUrl = {
                siteUrl: newUrl
            }
            axios.post(beLink+'sites/add',siteUrl)
                    .then(res => {
                        setLoading(false)
                        setScrapeState(res.data)
                        // setTimeout(()=>{
                        //     setScrapeState(-1)
                        // },2000)
                        setNewUrl("")
                        console.log(res.data)
                    });
        }
    }
    
    function ListItemLink(props) {
        return <ListItem button component="a" {...props} />;
    }

    const sitesList = sites.map(site =>
        {
            return(<ListItemLink href={site.url}>
            <ListItemText primary={site.url} />
          </ListItemLink> 
            )
        
        })
        

        
    return(
   <MuiThemeProvider>
       <main>

       <br/>
       
     

        <br/>
            <div>
                    <TextField 
                        floatingLabelText="Add url to scrape list"
                        defaultValue={newUrl}
                        onChange={handleChange}
                        onKeyPress={_handleKeyDown}
                    /> 


                
            </div>


           
            <br/>
            <List className="half" component="nav" aria-label="secondary mailbox folders">
           {sitesList}
           
            </List>
            
            <br/>

            <RaisedButton className={!loading? '' : 'no-show'} onClick={scrape}>
            Scrape
        </RaisedButton>
       
        <Loading status={scrapeState}/>
        <br/>
        <div className= {loading? 'lds-default' : 'no-show'}><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>

        </main>
        </MuiThemeProvider>
        
    )
    
}