import React, {Component, useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import Item from './item.component'
import '../styles/itemlist.css'
import List from './demo-list.component'
import Loading from './loading.component'
import BEserv from '../config/BEserv'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import TextField from 'material-ui/TextField'
import RaisedButton from 'material-ui/RaisedButton'


export default function ItemList(props) {
    
    const [items, setItems] = useState([])
    const [loading, setLoading] = useState(true)
    const [seen,setSeen] = useState(false)
   
    const beLink = BEserv.beServ
    const getData = status =>{

        setLoading(true)

        if(status==="seen"){
            axios.get(beLink+'/items/seen')
            .then(res => {
                    setSeen(true)
                    setLoading(false)
                    setItems(res.data)  
            })
            .catch( err => console.log(err))
        }

        else if(status==="unseen"){
            axios.get(beLink+'/items')
            .then(res => {
                    setSeen(false)
                    setLoading(false)
                    setItems(res.data)  
            })
            .catch( err => console.log(err))
        }
       
    }
    
    useEffect(() => getData("unseen"),[]);

    
    const markSeen = () =>{
        setLoading(true);
        axios.get(beLink+'/items/markseen')
                .then(res => {
                    setTimeout(()=>{
                    },2000)
                        axios.get(beLink+'/items')
                            .then(res => {
                                    setLoading(false)
                                    setItems(res.data)  
                            })
                            .catch( err => console.log(err))
                })
                .catch( err => console.log(err))
    }


    const itemList = items.map(item =>
        <Item item={item}  key={item._id}/>)
        const itemSize = items.length;
        
    return(
        <MuiThemeProvider>
            <React.Fragment>
                <main>
            <div className="photo-grid">
                <div
                    className="card">
                    <List getData={getData}/>
                </div>
                
                <div
                    className="card">
                        <h3 className="text-center"> {itemSize} items</h3>
                        <div className= {loading? 'lds-default' : 'no-show'}><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
                </div>
                <div
                    className="card">  
                     <a className={!seen? '' : 'no-show'} href="" onClick={markSeen}>
                        Mark all as Seen
                    </a>
                </div>
        
               
                

            </div>
          
            

            <div className="listThingy">
                <div className="container">

                    <div className="row">
                    {itemList}
                    </div>

	            </div>

            </div>
            </main>
            </React.Fragment>
        </MuiThemeProvider>
    )
}