import React from 'react';

function Item(props){
    return(
        <div className="col-md-4 product-grid">
            <div className="image">
                <a href={props.item.link}>
                    <img src={props.item.pic} className="w-100"/>
                    
                </a>
            </div>
            <h5 className="text-center">{props.item.name}</h5>
            <h5 className="text-center">{props.item.price}</h5>
        </div>
        
    )
}

export default Item;
